.stack-root {
    background-color: #2d4059;
    /* padding-top: 74px; */

    height: auto;
    width: 100vw;
    max-width: 100vw;
    min-height: 100vh;
    overflow: hidden;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    margin: auto;

    padding: 74px 15px 0 15px;
}

.stack-root .title, .descr, .definition, .type-title {
    color: white;
}

.stack-root .title {
    width: 100%;
    font-size: 2rem;
    text-align: center;
}

.stack-root .stack-type {
    border-bottom: 1px dashed gray;
    padding: 20px 0;
}


.stack-root .stack-type .type-title {
    font-size: 1.6rem;
}

.stack-root .stack-type img {
    /* width: 100%; */
    /* height: 30%; */
    max-height: 20rem;
    max-width: 100%;
    object-fit: contain;
    margin: 10px 0;
}

.stack-root .stack-type .definition {
    font-size: 1.3rem;
}

.stack-root .stack-type .descr {
    padding-left: 20px;
}

.stack-root .stack-type .tech {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: left;
    /* margin-bottom: 30px; */
}

.stack-root .stack-type .tech span {
    background-color: #ffd460;
    border-radius: 7px;
    color: #2d4059;
    font-weight: bold;
    font-size: 0.8rem;
    margin-right: 10px;
    margin-bottom: 5px;
    padding: 5px;
}

@media only screen and (min-width: 768px) {
    .stack-root .stack-type {
        width: 60%;
    }
}
.wpp-root {
    height: 50px;
    width: 50vw;

    position: fixed;
    bottom: 10px;
    left: 10px;

    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
}

.wpp-root #logo {
    height: 100%;
    width: auto;
    object-fit: contain;

    cursor: pointer;
}

.wpp-root #message {
    /* height: 100%; */
    font-size: 1rem;
    margin-left: 10px;
    text-align: center;
    margin-bottom: 0;
    color: white;
    font-weight: bold;

    /* background-color: white; */
    border: none;
    border-radius: 5px;

    padding: 10px 5px;
    visibility: hidden;
}

@media only screen and (min-width: 768px) {
    .wpp-root #message {
        visibility: hidden;
    }

    #logo:hover + #message {
        visibility: visible;
    }
}

